import Link from 'next/link'

const linkStyle = {
  marginRight: 15
}

const headerM = {
  width: '960px',
  backgroundColor: 'orange'
}

const Header = () => (
    <div style={headerM}>
      <ul>
        <li>
          <Link href="/">
            <a style={linkStyle}>Home</a>
          </Link>
        </li>
        <li>
        <Link href="/about">
          <a style={linkStyle}>About</a>
        </Link>
        </li>
      </ul>
    </div>
)

export default Header



